@import "../scssVariable/scssVariable.scss";

.main{
    display: grid;
    justify-items: center;
    // background-image: url('../../img/ghg5556.png');
    // background-size:cover;
    // background-position: center;
    margin-top: 80px;
}
.blockContact{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    margin: 60px 0px;
}
.AdressAndTel{
    display: grid;
    align-items: center;
}
.contact{
    width: 80%;
    background-color: rgba(0, 0, 0, 0.708);
    border-radius: 8px;
    font-size: 22px;
    color: rgb(255, 255, 255);
}
.map{
    background-color: rgba(129, 129, 129, 0.919);
    border-radius: 8px;
    height: 400px;
    width: 40vw;
}