@import "../scssVariable/scssVariable.scss";

@mixin cards{
    margin: 40px 20px 0px 20px;
    height: 500px;
    width: 340px;
    border-radius: 8px;
    background-size:cover;
    background-position: center;
    color: #dfdfdf;
}
.main{
    margin-top: 80px;
}
.cardField{
    display: flex;
    justify-content: center;
}
.cardText{
    display: grid;
    background-color: rgba(9, 104, 199, 0.56);
    font-size: 20px;
    border-radius: 8px 8px 0px 0px;
    height: 50px;
    align-content: center;
}
.card1{
    @include cards;
    background-image: url('../../img/personal.jpg');
    
}
.card2{
    @include cards;
    background-image: url('../../img/indiv2.jpg');
}
.card3{
    @include cards;
    background-image: url('../../img/group2.jpg');
}
