@import "../scssVariable/scssVariable.scss";
.main{
    display: grid;
    justify-items: center;
    margin-top: 80px;
    padding-bottom: 40px;
}
.blockSystem{
    display: grid;
    justify-items: center;
    background-image: url('../../img/1692379633_pushinka-top-p-sportivnii-zal-kartinki-krasivo-61.jpg');
    background-size:cover;
    background-position: center;
    width: 70%;
    border-radius: 8px;
}
.system{
    
    border-radius: 8px;
    background-color: rgba(32, 32, 32, 0.842);
    height: 400px;
    width: 70%;
    margin: 40px 0px 40px 0px;
}